	module.exports = {"data":{"markdownRemark":{"html":"<ul>\n<li>These rankings are based on activity on both Github as well as Stack Overflow and provides an indicator on where the community is focusing on. You can read more about this at <a href=\"http://redmonk.com/sogrady/2018/03/07/language-rankings-1-18/\">redmonk.com</a></li>\n</ul>\n<img src=\"http://sogrady-media.redmonk.com/sogrady/files/2018/03/lang.rank_.118-1024x726.png\" alt=\"Jan 2018 RedMonk Language Ratings\" width=\"350\" height=\"250\"/>\n<h2>Some highlights</h2>\n<ul>\n<li><code>Swift</code> has been one of the consistent movers up and is at #10 in the list now.</li>\n<li><code>Go</code> has seen a slight decline after holding steady for the last two quarters.</li>\n<li><code>Kotlin</code> has seen significant activity with a strong backing of the Spring Foundation and Spring 5 as well as in the mobile developer community.</li>\n<li><code>TypeScript</code> jumped three spots and is seen being used both for the client UI framework development as well as for server side. Good IDE support, type safety and advanced ES6/7 feature support.</li>\n<li><code>JavaScript</code> continues to be at the top. We do see most of the new projects using ES6/7 with Babel which also has as good IDE support as TypeScript.</li>\n</ul>","frontmatter":{"title":"RedMonk Programming Language Ranking for Jan 2018","author":"Tarun Sukhu <tsukhu@hcl.com>"}}},"pathContext":{"slug":"/data/posts/redmonkJan18/"}}

